import React, {useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {Card, Col, Form, Input, message, Row} from "antd";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import ApiService from "../../../../../../services/api.service";

function CreatePages() {

    const [form] = Form.useForm()
    const [code, setCode] = useState('')
    const [identifier, setIdentifier] = useState('')
    const [loading, setLoading] = useState(false)

    function onFinish() {
        setLoading(true)
        form.validateFields()
            .then((value) => {

                message.loading({
                    content: "Loading...",
                    key: "create-location",
                    duration: 2
                })
                new ApiService({
                    body: value,
                    url: `/api/v1/master-data/location`
                }).post()
                    .then((response) => {
                        message.success({
                            content: response?.message ?? "Successfully created",
                            key: "create-location",
                            duration: 2
                        })
                        setLoading(false)
                    })
                    .catch((err) => {
                        message.error({
                            content: err?.message ?? "Error: Some Error",
                            key: "create-location",
                            duration: 2
                        })
                        setLoading(false)
                    })
            })
            .catch((err) => {
                message.error({
                    content: err?.message ?? "Error: Validate fields",
                    key: "create-location",
                    duration: 2
                })
                setLoading(false)
            })
    }

    return (
        <React.Fragment>
            <HeroComponents
                loading={false}
                name={"Create"}
                positionName={['Master Data','Thin Client','Location']}
                thumbnail={null}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type: "disabled",
                            property: {
                                message: "Access Denied"
                            }
                        }}
                        config={{
                            loading: loading ?? false,
                            onClick: onFinish,
                            type: "primary", size: "small"
                        }}>
                        Save
                    </ButtonPermission>
                </>}
            />
            <ContainerTailwind>
                <div style={{marginTop: 90}}>

                    <Form
                        layout={'vertical'}
                        form={form}
                        initialValues={{
                            identifier: '',
                            data: {
                                name: "",
                                code: ""
                            },
                        }}
                    >
                        <Row gutter={24} justify={'start'}>
                            <Col span={16}>
                                <Card bordered={false}>
                                    <Form.Item
                                        rules={[{required: true, message: "Can't be empty!"}]}
                                        name={'identifier'} label={'Identifier'}
                                        value={identifier}
                                        hasFeedback
                                    >
                                        <Input
                                            placeholder={"Identifier is made unique or represents what should be input in the { display name } field"}
                                        />
                                    </Form.Item>
                                    <Form.Item
                                        rules={[{required: true, message: "Can't be empty!"}]}
                                        name={['data', 'name']} label={'Display Name'}
                                        hasFeedback
                                    >
                                        <Input placeholder={'Input your text for display name'}/>
                                    </Form.Item>
                                </Card>
                            </Col>
                            <Col span={8}>
                                <Card bordered={false}>
                                    <Form.Item
                                        rules={[{required: true, message: "Can't be empty!"}]}
                                        name={['data', 'code']} label={'Code'}
                                        hasFeedback
                                        value={code}
                                    >
                                        <Input
                                            placeholder={'Input your Code Unique'}
                                            onChange={(e) => {
                                                let value = `${e.target.value}`.replace(/ /g, '-')
                                                form.setFieldsValue({data: {code: value.toUpperCase()}})
                                            }}
                                        />
                                    </Form.Item>
                                </Card>
                            </Col>

                        </Row>
                    </Form>
                </div>
            </ContainerTailwind>

        </React.Fragment>
    )
}

CreatePages.propTypes = {}
CreatePages.defaultProps = {}

export default connect(() => {
    return {}
}, {})(React.memo(CreatePages))
